.header {
  width: 100%;
  position: fixed;
  z-index: 9999;
  background-color: rgba(12, 7, 4, 0.7);
  color: #FFF;
  text-align: center;
  &__button {
    @include css-lock(13, 16, 480, 960);
    margin-top: 1.1em; } }

.logo {
  float: left;
  text-align: left;
  line-height: 1.5em;
  &__title {
    @include H-l;
    @include css-lock(12, 20, 480, 960);
    margin-top: 0.75em;
 }    // font-size: em(20)
  &__name {
    @include H;
    @include css-lock(14, 22, 480, 960);
    // font-size: em(22)
    font-weight: 700;
    text-transform: uppercase;
    margin-bottom: 0.73em; } }
.telephone {
  margin-top: 1em;
  text-align: left;
  float: right;
  @include H-l;
  @include css-lock(10, 13, 480, 960);
  // font-size: em(13)
  &__title {
    opacity: 0.86;
    line-height: 1.8em; }
  &__number {
    @include H;
    @include css-lock(20, 28, 480, 960);
    // font-size: em(28)
    font-weight: 700;
    line-height: 1.05em; } }
