.top {
	background-color: #ccc;
	background-image: url(../img/top-bg.jpg);
	@include cover;
	color: #ffffff;
	padding-top: 79px;
	&__text {
		opacity: 0.9;
		@include H;
		@include css-lock(16, 22, 480, 960);
		line-height: 1.3em;
		// font-size: em(22)
		margin: 1.6em 1em 1.7em 50%; }

	&__button {
		margin-bottom: 4.45em;
		@include css-lock(13, 23, 480, 960);
		// font-size: 23px
		margin-left: 50%; }
	&__line {
		text-align: center;
		@include PTs-bi;
		@include css-lock(12, 24, 480, 960);
		// font-size: em(24)
		line-height: 2.3em;
		background-image: linear-gradient(to right, transparent 3%, rgba(203, 52, 97, 0.93) 15%, rgba(203, 52, 97, 0.93) 85%,  transparent 97%); }
	.title-h1 {
		margin-left: 50%;
		margin-top: 2.1em; } }
